@import url("https://fonts.googleapis.com/css?family=Rajdhani");
* {
  padding: 0;
  margin: 0; }

@font-face {
  src: url("/static/fonts/Starjout.ttf");
  font-family: "StarJedi"; }

button {
  background-color: transparent;
  border: none;
  font-family: 'Rajdhani', serif;
  color: #FFF;
  outline: none; }

input {
  font-family: 'Rajdhani', serif;
  outline: none; }

body {
  font-family: 'Rajdhani', cursive;
  color: #FFF;
  background-color: #000; }

h1 {
  font-family: "StarJedi", cursive;
  letter-spacing: 2px; }
  h1 img {
    height: 2rem; }

.fadeIn {
  opacity: 1;
  display: block;
  transition: opacity .5s ease-in, display 0s .5s linear; }

.fadeOut {
  opacity: 0;
  display: none;
  transition: opacity .5s ease-in, display 0s .5s linear; }

@keyframes bounce {
  to {
    transform: translateY(0); }
  50% {
    transform: translateY(3px); }
  to {
    transform: translateY(0); } }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  50% {
    transform: rotate(360deg); }
  to {
    transform: rotate(0deg); } }

.loading, .home-overlay, .game-bg, .detail, .finish {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8); }

.loading-content, .detail-content, .finish-content {
  background-color: rgba(0, 0, 0, 0.9);
  width: 600px;
  height: 400px;
  top: calc(50% - 200px);
  padding: 1rem;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative; }

.container {
  max-width: 1170px;
  margin: 0 auto; }

.footer {
  margin-top: 2rem;
  text-align: center; }

.loading-title {
  text-align: center;
  font-size: 2rem; }

.loading-img {
  height: 10rem;
  display: block;
  margin: 3rem auto;
  animation: rotate 10s infinite;
  opacity: .7; }

.menu {
  position: absolute;
  top: 1.5rem;
  left: 1rem;
  z-index: 99; }
  .menu-list {
    list-style: none;
    display: flex; }
    .menu-list-item {
      text-transform: uppercase;
      margin-right: .7rem;
      font-size: 1.3rem;
      cursor: pointer;
      transition: all .3s ease; }
      .menu-list-item:hover {
        border-bottom: thin solid #FFF; }
      .menu-list-item a {
        color: #FFF;
        text-decoration: none; }

.home-header {
  height: 100vh;
  position: relative; }

.home-mainImg {
  height: 20rem;
  display: block;
  margin: 1rem auto;
  margin-bottom: -3rem; }

.home-title {
  text-align: center;
  font-size: 3rem; }

.home-txt {
  text-align: center;
  margin-bottom: 2rem; }

.home-start {
  display: block;
  width: 12rem;
  text-align: center;
  font-size: 2rem;
  border: thin solid #FFF;
  padding: .5rem 0;
  margin: 0 auto;
  cursor: pointer;
  color: #FFF;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold; }

.home-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
  background-image: url("/static/img/bg-effect.png"); }

.home-clip {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -2;
  transform: scale(2, 2); }

.home-goHow {
  position: absolute;
  bottom: 2rem;
  width: 100%; }
  .home-goHow-down {
    height: 4rem;
    display: block;
    margin: 0 auto;
    animation: bounce 1s ease-in-out infinite;
    cursor: pointer; }
  .home-goHow-txt {
    text-align: center;
    font-weight: bold;
    font-size: 1.5rem; }

.home-how {
  background-color: #000;
  padding-top: 1rem; }
  .home-how-title {
    font-size: 2rem;
    margin: 1rem 0; }
  .home-how p {
    font-size: 1.2rem;
    margin-bottom: 1rem; }
    .home-how p:last-child {
      margin-bottom: 0; }
    .home-how p img {
      height: 1.2rem; }

.game-header {
  padding: .5rem 0;
  position: relative; }
  .game-header-pic-img {
    height: 6.4rem;
    align-self: flex-start; }
  .game-header-title {
    font-size: 4rem;
    line-height: 3.2rem;
    text-align: center; }
  .game-header-counter {
    position: absolute;
    top: 1rem;
    right: 1rem;
