    .game-header-counter span {
      width: 4rem; }
  .game-header-points {
    position: absolute;
    top: 3rem;
    right: 1rem;
    font-size: 3rem; }
  .game-header-txt {
    text-align: center;
    margin-top: 2rem;
    font-size: 1.5rem; }

.game-bg {
  width: 200vw;
  height: 200vh;
  top: -50vh;
  left: -50vw;
  background-image: url(https://ak7.picdn.net/shutterstock/videos/28045867/thumb/1.jpg);
  z-index: -1;
  animation-duration: 30s;
  animation-iteration-count: infinite; }

.game-content {
  margin-top: 2rem;
  display: flex;
  align-items: center; }
  .game-content-container {
    width: 90%;
    overflow: hidden; }
  .game-content-button {
    width: 10%;
    cursor: pointer; }
    .game-content-button img {
      height: 2rem; }
  .game-content-list {
    display: flex;
