      margin-bottom: 2rem;
      background-color: rgba(255, 255, 255, 0.2);
      color: #000; }
      @media (max-width: 1440px) {
        .game-content-list-item {
          width: 15rem;
          margin-right: calc((100% - 60rem) / 3); } }
      .game-content-list-item:nth-of-type(4n) {
        margin-right: 0; }
      .game-content-list-item.correct {
        background-color: rgba(0, 255, 0, 0.1);
        color: #FFF;
        text-align: center; }
      .game-content-list-item.wrong {
        background-color: rgba(255, 0, 0, 0.1);
        color: #FFF;
        text-align: center; }
      .game-content-list-item-pic {
        border-bottom: thin solid #FFF;
        height: 240px;
        overflow: hidden; }
        @media (max-width: 1440px) {
          .game-content-list-item-pic {
            height: 180px; } }
        .game-content-list-item-pic-img {
          width: 100%; }
      .game-content-list-item-footer-container {
        display: flex;
        width: 200%;
        transition: all .5s ease; }
        .game-content-list-item-footer-container.show-input {
          transform: translateX(-50%); }
      .game-content-list-item-controllers {
        display: flex;
        justify-content: center;
        width: 100%; }
      .game-content-list-item-button {
        margin-top: .5rem;
        cursor: pointer;
        opacity: .7;
        transition: all .3s ease; }
        .game-content-list-item-button img {
          height: 2rem; }
        .game-content-list-item-button:hover {
          opacity: 1; }
        .game-content-list-item-button-response {
          margin-left: 2rem; }
      .game-content-list-item-name {
        display: flex;
        width: 100%;
        align-items: center; }
        .game-content-list-item-name.show {
          opacity: 1; }
        .game-content-list-item-name-back {
          height: 100%;
          cursor: pointer;
          width: 10%;
          text-align: center; }
          .game-content-list-item-name-back-img {
            height: 1.5rem;
            position: relative;
            top: calc(50% - .75rem); }
        .game-content-list-item-name-input {
          width: 78%;
          font-size: 1rem;
          background-color: transparent;
          border: none;
          border-bottom: thin solid #FFF;
          color: #FFF; }
        .game-content-list-item-name-send {
          width: 10%;
          font-size: 1rem;
          cursor: pointer;
          height: 100%;
          margin-left: 2%; }
          .game-content-list-item-name-send img {
            height: 1.5rem;
            position: relative;
            top: 2px; }

.detail-content {
  min-height: 300px;
  height: auto; }
  .detail-content-close {
    position: absolute;
    height: 1rem;
    top: 1rem;
    right: 1rem;
    cursor: pointer; }

.detail-title {
  text-align: center;
  margin-bottom: .5rem; }

.detail-photo {
  float: left;
  width: 300px;
  overflow: hidden;
  margin-right: 1rem; }
  .detail-photo-img {
    width: 100%; }

.detail-text {
  margin-left: 1rem; }

.finish-content {
  background-color: rgba(0, 0, 0, 0.9); }

.finish-point {
  font-size: 5rem;
  text-align: center; }

.finish-close {
  position: absolute;
  height: 1rem;
  top: 1rem;
  right: 1rem; }

.finish-title {
  text-align: center; }

.finish-form-content {
  margin-bottom: 2rem; }

.finish-form-label {
  display: block;
  font-size: 1rem;
  margin-bottom: .3rem; }

.finish-form-input {
  background-color: transparent;
  border: none;
  font-size: 1rem;
  border-bottom: thin solid #FFF;
  width: 100%;
  color: #FFF;
  font-family: 'Lora', serif; }

.finish-error {
  color: red;
  text-align: center; }

.finish-save {
  display: block;
  width: 12rem;
  border: thin solid #FFF;
  font-size: 1.7rem;
  margin: 0 auto;
  cursor: pointer;
  border-radius: 3px; }

.score-header-subTitle {
  text-align: center;
  font-size: 2rem; }

.score-list {
  list-style: none;
  width: 1170px;
  margin: 2rem auto;
  padding: 1rem;
  background-color: rgba(20, 20, 20, 0.8);
  border: thin solid #FFF;
  border-radius: 10px; }
  .score-list-item {
    margin-bottom: 1rem;
    font-size: 1.2rem; }



